body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  /* height: 40vmin; */
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.sudoku-board button.value {
    height: 54px;
    width: 54px;
    border: 0;
    margin: 0;
    padding: 0;
    font-size: 36px;
    cursor: pointer;
    background: transparent;
    position: absolute;
    top: 0px;
    left: 0px;
}

.sudoku-board button.candidate {
    height: 18px;
    width: 18px;
    border: 0;
    margin: 0;
    padding: 0;
    font-size: 12px;
    float: left;
    cursor: pointer;
    background: transparent;
}

.sudoku-board button.candidate:hover {
    background: grey;
}

.sudoku-board button.placeholder {
    height: 18px;
    width: 18px;
    border: 0;
    margin: 0;
    padding: 0;
    font-size: 12px;
    float: left;
    background: transparent;
}

div.wrapper {
    height: 54px;
    width: 54px;
    position: relative;
}

.sudoku-board button.removed {
    background: lightgray
}

.sudoku-board {
    background: white;
    border-spacing: 0;
    margin: 0 auto;
    color: black;
}

.sudoku-board td{
    padding: 0;
    margin: 0;
    border-bottom: 1px solid grey;
    border-right: 1px solid grey;
}

.sudoku-board td:nth-child(3n){
    border-right: 2px solid black;
}

.sudoku-board tr:nth-child(3n) td{
    border-bottom: 2px solid black;
}

.sudoku-board tr:last-child td{
    border-bottom: 2px solid grey;
}

.sudoku-board tr:first-child td{
    border-top: 2px solid grey;
}

.sudoku-board td:last-child{
    border-right: 2px solid grey;
}

.sudoku-board td:first-child{
    border-left: 2px solid grey;
}

.sudoku-board td.default {
    background: lightgray;
}

.button-bar{
    text-align: center;
}

.solved-bar{
    text-align: center;
}
